import React, { useEffect, useState, useCallback } from 'react';
import { useSelector, useDispatch } from "react-redux"
import { useToasts } from 'react-toast-notifications';
import { Button, IconButton, Switch, TextField } from '@material-ui/core';
import { actions } from '../redux/_actions';
import Layout from './layout';
import api from '../api';

import './profile.scss';
import { ContactMail, ExitToApp, Feedback, HorizontalSplit, LocalLibrary, PersonPin, Receipt, RotateLeft, Save, Security, Send } from '@material-ui/icons';

const Profile = () => {
  const [sendStatus, setSendStatus] = useState(false);
  const [secureUid, setSecureUid] = useState('');
  const [interv, setInterv] = useState(null);
  const [shipping, setShipping] = useState({});
  const [feedback, setFeedback] = useState({});

  const { email, secure } = useSelector(state => state.auth);
  const dispatch = useDispatch();
  const { addToast } = useToasts();

  const showToast = useCallback((message, appearance = 'error') => {
    addToast(message, { appearance, autoDismiss: true });
  }, [addToast]);

  useEffect(() => {
    if (sendStatus && !interv) {
      const checkSecureVerified = () => {
        api.checkSecureVerified(secureUid)
          .then((data) => {
            if(data.success) {
              clearInterval(interv);
              setInterv(null);
              setSendStatus(false);
              dispatch(actions.setSecure(1));
            }
          })
          .catch(err => showToast(err.message));
      }

      if(interv) clearInterval(interv)
      const interval = setInterval(checkSecureVerified, 5000);
      setInterv(interval)
    }
  }, [sendStatus, interv, secureUid, dispatch, showToast])

  const setSecure = () => {
    api.setSecure()
      .then((data) => {
        console.log(data)
        setSecureUid(data.uid)
        setSendStatus(true);
      })
      .catch(err => showToast(err.message));
  }

  const signout = () => {
    dispatch(actions.signout())
  }

  const shippingValueChanged = (e) => {
    shipping[e.target.name] = e.target.value;
    setShipping({ ...shipping });
  }
  const feedbackValueChanged = (e) => {
    feedback[e.target.name] = e.target.value;
    setFeedback({ ...feedback });
  }

  return (
    <Layout>
      <div className='profile-page'>
        <div className='top-buttons-wrapper'>
          <div className='email-header'>{email}</div>
          <div className='signout-button' onClick={signout}>
            <ExitToApp />
            <span>Signout</span>
          </div>
        </div>
        <div className='profile-settings'>
          <div className='left-side'>
            <div className='switch-wrapper'>
              <Security />
              <div className='text-content'>Secure my Account</div>
              <Switch color='primary' />
            </div>
            <div className='switch-wrapper'>
              <Receipt />
              <div className='text-content'>Include tax in Orders</div>
              <Switch color='primary' />
            </div>
            <div className='switch-wrapper'>
              <HorizontalSplit />
              <div className='text-content'>Include Promo Codes</div>
              <Switch color='primary' />
            </div>
            <div className='switch-wrapper'>
              <LocalLibrary />
              <div className='text-content'>Personal Shopping Recommendations</div>
              <Switch color='primary' />
            </div>
            <div className='switch-wrapper'>
              <PersonPin />
              <div className='text-content'>Anonymous Shopping</div>
              <Switch color='primary' />
            </div>
          </div>
          <div className='right-side'>
            <div className='form-wrapper'>
              <div className='switch-wrapper'>
                <ContactMail />
                <div className='text-content'>Shipping Address</div>
                <div className='toolbox'>
                  <IconButton size='small' color='inherit'><RotateLeft /></IconButton>
                </div>
              </div>
              <div className='form-content'>
                <div className='two-inputs'>
                  <TextField className='form-input' label='Country' size='small' variant='outlined' fullWidth color='primary' name='country' onChange={shippingValueChanged} />
                  <TextField className='form-input' label='City' size='small' variant='outlined' fullWidth color='primary' name='city' onChange={shippingValueChanged} />
                </div>
                <TextField className='form-input' label='Address Line 1' size='small' variant='outlined' fullWidth color='primary' name='address1' onChange={shippingValueChanged} />
                <TextField className='form-input' label='Address Line 2' size='small' variant='outlined' fullWidth color='primary' name='address2' onChange={shippingValueChanged} />
                <TextField className='form-input' label='Zip Code' size='small' variant='outlined' fullWidth color='primary' name='zipcode' onChange={shippingValueChanged} />
                <div className='bottom-buttons'>
                  <Button variant='contained' color='primary' size='small' startIcon={<Save />}>Save</Button>
                </div>
              </div>
            </div>
            <div className='form-wrapper'>
              <div className='switch-wrapper'>
                <Feedback />
                <div className='text-content'>Give us Feedback</div>
                <div className='toolbox'>
                  <IconButton size='small' color='inherit'><RotateLeft /></IconButton>
                </div>
              </div>
              <div className='form-content'>
                <TextField className='form-input' label='Feedback' size='small' variant='outlined' fullWidth color='primary' name='comment' multiline rows={4} onChange={feedbackValueChanged} />
                <div className='bottom-buttons'>
                  <Button variant='contained' color='primary' size='small' startIcon={<Send />}>Send</Button>
                </div>
              </div>
            </div>
          </div>
        </div>
        {/* <div className='input-label'>{email}</div>
        {secure ?
          <div className="input-label">Your account requires email verification to login</div> :
          <>
            {sendStatus ?
              <div className="input-label">Verification email sent</div> :
              <button className="input-button" onClick={setSecure}>Security</button> }
          </>
        } */}
      </div>
    </Layout>
  )
}

export default Profile;
